  getLocationOnBasisOfLatitudeandLongitude(latitude, longitude, control, i) {
    this.geoCoder = new google.maps.Geocoder;
    this.geoCoder.geocode({ 'location': { lat: latitude, lng: longitude } }, (results, status) => {
      if (status === 'OK') {
        if (results[0]) {
          control.controls[i].controls['mapaddress'].setValue(results[0].formatted_address);
        } else {
          control.controls[i].controls['mapaddress'].setValue('No Location Found');
        }
      } else if (status === 'OVER_QUERY_LIMIT') {
        setTimeout(() => {
          this.getLocationOnBasisOfLatitudeandLongitude(latitude, longitude, control, i)
      }, 1500);
      } else {
        // window.alert('Geocoder failed due to: ' + status);
        control.controls[i].controls['mapaddress'].setValue('');

      }
    });
  }
  draggEnded(event, i) {
    const control: any = this.gymeditForm.controls.gymbranches;
    if (event) {
      control.controls[i].controls['latitude'].setValue(event.coords.lat)
      control.controls[i].controls['longitude'].setValue(event.coords.lng)
      this.getLocationOnBasisOfLatitudeandLongitude(event.coords.lat, event.coords.lng, control, i)
    }

  }

  getDuplicatebranch(branchList: any) {
    let c = 0;
    for (let i = 0; i < branchList.length; i++) {
      for (let j = 0; j < branchList.length; j++) {
        if (i != j) {
          if (branchList[i].address == branchList[j].address && branchList[i].address != '' && branchList[j].address != '') {
            c = c + 1;
          }
          if (c > 1) {
            return false;
          }
        }
      }
    }
    if (c == 0) {
      return true;
    } else {
      return false;

    }
  }
  updateGymbyId() {
    this.updateResponseResult = 0;
    let isBioIsValid = true;
    let isBioInvalid = true;
    this.isDuplicateBranch = false;
    this.markFormGroupTouched(this.gymeditForm)
    const branchList = JSON.parse(JSON.stringify(this.gymeditForm.value.gymbranches));
    // if (!this.getDuplicatebranch(branchList)) {
    //   this.isDuplicateBranch = true;
    //   return false;

    // }
    if (branchList.length > 0) {
      if (this.ValidateLocation(branchList) === false) {
        this.openconfirmpopup(Constants.ValidLocalityMessage + this.errorLocationSelected.join(', '), 'invalid location');
        return false;
      }
      if (this.FieldValidate(branchList) === false) {
        this.isFieldValid = true;
        return false;
      }
    }

    const biostr = this.gymeditForm.value.bio;
    if (biostr && (!biostr.replace(/\s/g, '').length || biostr.replace(/\s/g, '').length > 600)) {
      isBioIsValid = false;
      isBioInvalid = false;
    }
    if (this.gymeditForm.valid && isBioIsValid) {
      if (!this.main_locations) {
        this.main_locations = this.gymeditForm.value.main_location
      }

      if (this.isValidOption) {
        return false;
      }

      this.http_user.updateNewGymById(LocalStorageService.getUserId(), this.gymeditForm.value.studioName, this.gymeditForm.value.firstName,
      this.gymeditForm.value.lastName, this.gymeditForm.value.gender, this.main_locations, this.gymeditForm.value.bio,
       this.gymeditForm.value.website, this.gymeditForm.value.phone, branchList, this.deleteBranchIdList, this.activitiesList,
        this.deletedActivitiesList, this.latitude, this.longitude, this, true)
    } else {
      if (!isBioIsValid) {
        this.showAlert(44);
        if (!isBioInvalid) {
          this.errorMessage = Constants.bioExceedMessage;
        } else {
          this.errorMessage = Constants.bioEditMessage;
        }
      } else {
        if (branchList.length > 0) {
          this.isFieldValid = true;

        }

      }
    }
  }
