  ValidateLocation(value: any) {
    this.latlnglocation = false;
    this.errorLocationSelected = [];
    if (value) {
      const list: any[] = [];
      for (let i = 0; i < value.length; i++) {
        const address: string = value[i].mapaddress;
        const inValid: boolean = value[i].inValid;
        if (inValid === true) {
          this.latlnglocation = true;
          this.errorLocationSelected.push(value[i].branchName)
        }

      }

    }
    return !this.latlnglocation;

  }

